import { _decorator, Component, Node, Prefab, instantiate, log, Gradient, Graphics, EventTouch, v3 } from 'cc';
import { level_progress_bar } from '../../transiton/level_progress_bar';
const { ccclass, property } = _decorator;

@ccclass('level4_science')
export class level4_science extends Component {
    @property(level_progress_bar)
    levelBarUI: level_progress_bar = null;
    @property(Node)
    level: Node = null;
    @property(Prefab)
    gPrefab: Prefab = null;
    @property(Node)
    area: Node = null;

    private graphics = null;
    private draw = null;
    start() {
        this.initGraphic();
        this.draw.on(Node.EventType.TOUCH_START, this.onTouchStart.bind(this));
        this.draw.on(Node.EventType.TOUCH_START, this.onTouchMove.bind(this));
        this.draw.on(Node.EventType.TOUCH_START, this.onTouchEnd.bind(this));
    }
    initGraphic(){
        this.draw = instantiate(this.gPrefab);
        let nodeG = this.draw;
        this.graphics = nodeG.getComponent(Graphics);
        let g = this.graphics;
        g.lineWidth = 10;
        // this.graphics = 
    }
    onTouchStart(event: EventTouch){
        let p = event.getUILocation();
        let out = v3();
        this.area.inverseTransformPoint(out, v3(p.x, p.y, 0));
        let g = this.graphics;
        g.moveTo(out.x, out.y);
        g.stroke();
    }
    onTouchMove(event: EventTouch){
        let p = event.getUILocation();
        let out = v3();
        this.area.inverseTransformPoint(out, v3(p.x, p.y, 0));
        let g = this.graphics;
        g.lineTo(out.x, out.y);
        g.stroke();
    }
    onTouchEnd(){
        
    }

    update(deltaTime: number) {
        
    }
}


